<template>
    <section class="wrap w100 bg-default">
        <HeaderLayout></HeaderLayout>
        <section class="w c-h pdt5" :style="{minHeight:minHeight+'px'}">
            <ul>
                <li class="article-item mg10" v-for="item in icons">
                    <a class="article-item-header clickable shadow"
                       :href="item.masterHref" target="_blank">
                        <img :src="item.icon"/>
                        <a class="article-item-header-more" :href="item.href">查看更多</a>
                    </a>
                    <ul>
                        <li class="article-item-content" v-for="(article,index) in articles(item)">
                            <div class="article-item-content-wrap clickable shadow"
                                 :class="[index%2===1?'right':'left']">
                                <h2>
                                    <a :href="article.href" target="_blank">{{article.title}}</a>
                                </h2>
                                <p class="mgt10" v-if="article.desc">
                                    <span>{{article.desc}}</span>
                                </p>
                                <p class="mgt10 text-gray" v-if="article.ref||article.lang">
                            <span v-if="article.ref"
                                  class="pdr20"><em
                                    style="background-color: #0366D6;"></em>来源：{{article.ref === 'source' ? '站内项目' : '站外大牛'}}</span>
                                    <span v-if="article.lang" class="pdl20"><em
                                            style="background-color: #B07219;"></em>语言：{{article.lang}}</span>
                                </p>
                                <div class="image mgt10" v-if="article.image">
                                    <img :src="article.image"/>
                                </div>
                                <p class="mgt10 text-gray">
                                    <span>{{article.create}}</span>
                                </p>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </section>
        <FooterLayout></FooterLayout>
    </section>
</template>
<script>
    import project from '../../resource/project.json';
    import toutiao from '../../resource/toutiao.json';
    import wechat from '../../resource/wechat.json';
    import { HeaderLayout, FooterLayout } from '../../../utils/web/Components';
    import {} from '../../../utils/web/Utils';
    import { mapActions, mapGetters } from 'vuex';

    export default {
        props: [],
        data () {
            return {
                icons: [
                    {
                        icon: 'data:image/png;base64,',
                        articles: project,
                        href: '/article/project.html',
                        masterHref: 'https://github.com/ChangedenCZD'
                    },
                    {
                        icon: 'data:image/png;base64,',
                        articles: toutiao,
                        href: '/article/toutiao.html',
                        masterHref: 'http://files.changeden.net/images/toutiao_qrcode.png'
                    },
                    {
                        icon: 'data:image/png;base64,',
                        articles: wechat,
                        href: '/article/wechat.html',
                        masterHref: 'http://files.changeden.net/images/wechat_qrcode.png'
                    }
                ]
            };
        },
        created () {
        },
        updated () {
        },
        watch: {
            bodyWidth () {
            }
        },
        methods: {
            ...mapActions([]),
            articles (item) {
                return item.articles.slice(0, 2);
            }
        },
        computed: {
            ...mapGetters({
                bodyWidth: 'bodyWidth',
                minHeight: 'minHeight'
            })
        },
        components: {
            HeaderLayout,
            FooterLayout
        }
    };
</script>
<style lang="scss" scoped>
    @import '../../scss/default.scss';
    @import '../../assets/css/common/material-design.css';

    .wrap {
        margin-top: 49px;
    }

    .article-item {
        max-width: 800px;
        margin: $s10 auto;
    }

    .article-item-header {
        padding: 10px 12px;
        display: block;
        position: relative;
        .article-item-header-more {
            position: absolute;
            right: 12px;
            top: 0;
            font: normal 14px/44px "Microsoft Yahei";
        }
        img {
            width: 24px;
            height: 24px;
            display: table-cell;
        }
    }

    .article-item-content {
        display: inline-grid;
        width: 50%;
        h2 {
            padding: 5px 0;
        }
        em {
            width: 7px;
            height: 7px;
            margin-right: 7px;
            display: inline-block;
            border-radius: 50%;
        }
        .article-item-content-wrap {
            padding: 12px;
            margin: 6px 0;
        }
        .article-item-content-wrap.left {
            margin-right: 3px;
        }
        .article-item-content-wrap.right {
            margin-left: 3px;
        }
        .image {
            text-align: center;
            img {
                max-width: 100%;
            }
        }
    }

    .article-item-footer {
        font-size: 12px;
        padding: 6px 12px;
    }

</style>


